import React, { useRef } from "react"

// import { Link } from "gatsby"
import styled from "styled-components"

import type Post from "~/src/types/Post"

import Card from "./card"
import useInfiniteScroll from "./useInfiniteScroll"

interface PostGridProperties {
  posts: Post[]
}

const PostGrid: React.FC<PostGridProperties> = ({ posts }) => {
  const scrollEdgeReference = useRef<HTMLDivElement>(null)
  const currentList = useInfiniteScroll({
    posts,
    scrollEdgeRef: scrollEdgeReference,
    maxPostNum: 10,
    offsetY: 200,
  })

  return (
    <Grid role="list">
      {currentList.map(data => {
        const {
          id,
          // slug,
          title,
          instagram,
          telegram,
          twitter,
          website,
          wikipedia,
          imdb,
          linkedin,
          facebook,
          tiktok,
          youtube,
          desc,
          date,
          category,
          subcategory,
          alt,
        } = data
        // const ariaLabel = `${title} - ${category} - Posted on ${date}`
        return (
          <List key={id} role="listitem">
            <Card
              alt={alt}
              category={category}
              subcategory={subcategory}
              title={title}
              instagram={instagram}
              telegram={telegram}
              twitter={twitter}
              website={website}
              wikipedia={wikipedia}
              imdb={imdb}
              linkedin={linkedin}
              facebook={facebook}
              tiktok={tiktok}
              youtube={youtube}
              desc={desc}
              date={date}
            />
          </List>
        )
      })}
      <div ref={scrollEdgeReference} />
    </Grid>
  )
}

const Grid = styled.ul`
  // display: grid;
  // grid-gap: var(--grid-gap-xl);
  // grid-template-columns: repeat(2, 1fr);
  list-style: none;

  & > li {
    margin-bottom: 0;
  }

  @media (max-width: ${({ theme }) => theme.device.sm}) {
    grid-gap: var(--grid-gap-lg);
  }
`

const List = styled.li`
  box-sizing: border-box;
  grid-column: span 1;

  // a {
  //   display: block;
  //   height: 100%;
  // }

  & .gatsby-image-wrapper {
    transition:
      opacity 1s ease-out,
      transform 0.5s ease;
  }

  a:hover,
  a:focus {
    .gatsby-image-wrapper {
      transform: scale(1.03);
    }
  }

  @media (max-width: ${({ theme }) => theme.device.sm}) {
    grid-column: span 2;
  }
`

export default PostGrid
